* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

root {
  height: 14px;
  width: 14px;
  font-size: 14px;
}

.container_all {
  display: flex;
  background: #FDFC47;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #FDFC47, #24FE41);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #FDFC47, #24FE41);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.left_side {
  height: 100vh;
  width: 70vw;
  display: flex;
  flex-direction: column;
}

.left_side .left_side_upper {
  width: 100%;
  height: 70%;
}

.left_side .left_side_bottom_all {
  width: 100%;
  height: 30%;
  display: flex;
  flex-direction: column;
}

.left_side .left_side_bottom_all .left_side_bottom_question {
  height: 30%;
  width: 100%;
  background-color: red;
  display: flex;
  justify-content: center;
  align-items: center;
}

.left_side .left_side_bottom {
  width: 100%;
  height: 70%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
  flex-direction: column;
}

.left_side .left_side_bottom .A:hover, .left_side .left_side_bottom .B:hover, .left_side .left_side_bottom .C:hover, .left_side .left_side_bottom .D:hover {
  transition: 0.5s;
}

.left_side .left_side_bottom .A, .left_side .left_side_bottom .B, .left_side .left_side_bottom .C, .left_side .left_side_bottom .D {
  width: 47%;
  height: 34%;
  border-radius: 30px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 10px 5px 10px 5px;
}

.left_side .left_side_bottom .A {
  background-color: #76A665;
}

.left_side .left_side_bottom .B {
  background-color: #214154;
}

.left_side .left_side_bottom .C {
  background-color: #FFDD5C;
}

.left_side .left_side_bottom .D {
  background-color: orangered;
}

.left_side .left_side_bottom .answers {
  font-weight: 600;
  font-size: 2rem;
}

.right_side {
  width: 30vw;
  height: 100vh;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
}

.right_append {
  width: 90%;
  border-radius: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 5px 10px 5px;
  background-color: red;
  color: white;
}

.right_append_question {
  font-size: 1.5rem;
}
