* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
root {
  height: 14px;
  width: 14px;
  font-size: 14px;
}
.container_all{
  display: flex;
  background: #FDFC47;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #FDFC47,#24FE41);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right,#FDFC47, #24FE41); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */


}
.left_side{
  height: 100vh;
  width: 70vw;
  //box-sizing: border-box;
  //border: 1px solid black;
  //background-color: black;
  display: flex;
flex-direction: column;

  .left_side_upper{
    width: 100%;
    height: 70%;
    //background-color: yellow;
  }
  .left_side_bottom_all{
    width: 100%;
    height: 30%;
    display: flex;
    flex-direction: column;
.left_side_bottom_question{
  height: 30%;
  width: 100%;
  background-color: red;
  display: flex;
  justify-content: center;
  align-items: center;
}
  }
  .left_side_bottom{
    width: 100%;
    height: 70%;
    //background-color: green;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
flex-wrap: wrap;
    flex-direction: column;



    .A:hover , .B:hover , .C:hover , .D:hover{

      transition: 0.5s;
    }




    .A , .B , .C , .D{
      width: 47%;
      height: 34%;
      border-radius: 30px;
      display: flex;
      justify-content: flex-start;
align-items: center;
      padding: 10px 5px 10px 5px;
    }
    .A{
      background-color: #76A665;
    }
    .B{
      background-color: #214154;
    }
    .C{
      background-color: #FFDD5C;
    }
    .D{
      background-color: orangered;
    }
.answers{
  font-weight: 600;
  font-size: 2rem;
}



  }


}










